import { Component, OnInit, Input } from '@angular/core';
import { DialogService } from "ng2-bootstrap-modal";

/* Services */
import { NeedModalComponent } from '../../modals/need-modal/need-modal.component';
import { ProjectsService } from '../../../Services/Projects/projects.service';

@Component({
	selector: 'app-create-need',
	templateUrl: './create-need.component.html',
	styleUrls: [
		'./create-need.component.scss',
		'../../../../public/styles/need-card.scss'
	]
})
export class CreateNeedComponent implements OnInit {

	@Input() project 	: Object
	openings 			: Object[] = []
	showOpenings 		: boolean = false

	constructor(private dialogService: DialogService,
				private ProjectsService: ProjectsService) { }

	ngOnInit() {
		this.openings = this.project['openings'];
	}

	transformTags(tags) {
		if (tags)
			return tags.split(',');
		else
			return []
	}

	deleteOpening(id, index) {
		this.ProjectsService.deleteProjectOpening(id, {}).subscribe( res => {
			this.openings.splice(index, 1);
		});
	}

	/* ShowSearchModal */
	showCreateNeedModal(opening, index) {
		let disposable = this.dialogService.addDialog(NeedModalComponent, {
			project_id 	: this.project['id'],
			opening 	: opening
	    }).subscribe( (need) => {
	    	if (need) {
	    		if (opening) {
	    			this.openings[index] = need
	    		} else {
	    			this.openings.push(need);
				}
				console.log(this.openings);
			}
		});
    }

}



