import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CloudinaryOptions, CloudinaryUploader } from 'ng2-cloudinary';

/* Services */
import { PicturesService } from '../../../Services/Pictures/pictures.service';

@Component({
	selector: 'app-create-story',
	templateUrl: './create-story.component.html',
	styleUrls: ['./create-story.component.scss']
})
export class CreateStoryComponent implements OnInit {

	@Input() project 	: Object
	@Input() admin 		: boolean
	@Output() story		: EventEmitter<any> = new EventEmitter<any>()

	editorContent 		: string
	resume_1 			: string
	resume_2 			: string
	video 				: string
	type 				: string
	pictures 			: Array<string> = []

	public options: Object = {
		placeholderText: 'Edit Your Content Here!',
		events : {
			'froalaEditor.focus' : function(e, editor) {
			}
		}
	}

	cloudinaryMedia: any;

	uploader: CloudinaryUploader = new CloudinaryUploader(
		new CloudinaryOptions({ cloudName: 'dqpkpmrgk', uploadPreset: 'z7rzegb5', autoUpload: true })
	);

	constructor(private PicturesService: PicturesService) {
		this.uploadMedia();
	}

	ngOnInit() {
		this.initVariable();
	}

	initVariable() {
		this.resume_1 	= this.project['1st_description']
		this.resume_2 	= this.project['2nd_description']
		this.video 		= this.project['video']
		this.pictures 	= this.project['pictures']
		this.editorContent = this.project['about']
	}

	uploadMedia() {
		this.uploader.onSuccessItem = (item: any, response: string, status: number, headers: any) => {
			this.cloudinaryMedia 	= JSON.parse(response);
			if (this.type === 'video')
				this.video = this.cloudinaryMedia.secure_url
			else
				this.pictures.push(this.cloudinaryMedia.secure_url)
			this.emitChange();
		};

		this.uploader.onAfterAddingFile = (fileItem) => {
			this.uploader.uploadAll();
		};
	}

	setMediaType(type) {
		this.type = type
	}

	removeVideo() {
		this.video = ''
		this.emitChange();
	}

	removePictures(index) {
		this.pictures.splice(index, 1);
		this.emitChange();
	}


	onChange(type, newValue) {
		if (type === 'resume_1')
			this.resume_1 = newValue
		else
			this.resume_2 = newValue
		this.emitChange()
	}

	emitChange() {
		this.story.emit({
			resume_1 	: this.resume_1,
			resume_2	: this.resume_2,
			video 		: this.video,
			pictures 	: this.pictures,
		})
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}
}



