import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';

/* Custom Modules */
import { PipeModule } from '../../Pipes/pipe.module';
import { FooterModule } from '../footer/footer.module';
import { ModalModule } from '../modals/modal.module'
import { DirectivesModule } from '../../Directives/directives.module';

/* Components */
import { CreateProjectsComponent } from './create-projects.component';
import { CreateStoryComponent } from './create-story/create-story.component';
import { CreatePeopleComponent } from './create-people/create-people.component';
import { CreateNeedComponent } from './create-need/create-need.component';
import { CreateCommunityComponent } from './create-community/create-community.component'
import { SearchModalUserComponent } from '../modals/search-modal-user/search-modal-user.component';
import { NeedModalComponent } from '../modals/need-modal/need-modal.component';

/* Libraries */
import { FroalaEditorModule, FroalaViewModule } from 'angular2-froala-wysiwyg';
import { Ng2CloudinaryModule } from 'ng2-cloudinary';
import { FileUploadModule } from 'ng2-file-upload';
import { UiSwitchModule } from 'angular2-ui-switch'

/* Route */
import { CREATE_PROJECT_ROUTES } from '../../Routes/create-project.routes'

const cloudinaryConfiguration = {
	cloud_name: 'dqpkpmrgk',
	upload_preset: 'z7rzegb5'
}

@NgModule({
	imports: [
		CREATE_PROJECT_ROUTES,
		CommonModule,
		FormsModule,
		RouterModule,
		PipeModule,
		FooterModule,
		ModalModule,
		DirectivesModule,
		FileUploadModule,
		Ng2CloudinaryModule,
		UiSwitchModule,
		FroalaEditorModule.forRoot(),
        FroalaViewModule.forRoot(),
	],
	declarations: [
		CreateProjectsComponent,
		CreateStoryComponent,
		CreatePeopleComponent,
		CreateNeedComponent,
		CreateCommunityComponent
	],
	entryComponents: [
		SearchModalUserComponent,
		NeedModalComponent
	],
	providers: [
	]
})
export class CreateProjectsModule { }



