import { Component, OnInit, Input } from '@angular/core';
import { DialogService } from "ng2-bootstrap-modal";

/* Component */
import { SearchModalUserComponent } from '../../modals/search-modal-user/search-modal-user.component';
import { ProjectsService } from '../../../Services/Projects/projects.service';
import { PicturesService } from '../../../Services/Pictures/pictures.service';

@Component({
	selector: 'app-create-people',
	templateUrl: './create-people.component.html',
	styleUrls: ['./create-people.component.scss']
})
export class CreatePeopleComponent implements OnInit {


	@Input() project_id : number
	invites 			: Object[]

	constructor(private dialogService: DialogService,	
				private ProjectsService: ProjectsService,
				private PicturesService: PicturesService) { }

	ngOnInit() {
		this.getInviteToProject();
	}

	getInviteToProject() {
		this.ProjectsService.getInviteToProject(this.project_id).subscribe(	res => {
			this.invites = res.invitations
		});
	}

	deleteInviteToProject(id, index) {
		this.ProjectsService.deleteInviteToProject(id, {id: id}).subscribe( res => {
			if (res.success)
				this.invites.splice(index, 1);
		});
	}

	/* ShowSearchModal */
	showSearchModalUser() {
		let disposable = this.dialogService.addDialog(SearchModalUserComponent, {
			called_id : 0,
			project_id : this.project_id
	    }).subscribe( success => {
	    	if (success)
	    		this.getInviteToProject();
	    })
    }

    /* Transform picture */
    transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}

}



