/* Modules */
import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

/* Components */
import { CreateProjectsComponent } from '../Components/create-projects/create-projects.component';

/* Librairies */

const routes: Routes = [
	{ path: '', component: CreateProjectsComponent },
];

export const CREATE_PROJECT_ROUTES: ModuleWithProviders = RouterModule.forChild(routes);



