import { Component, OnInit, NgZone } from '@angular/core';
import { Router, ActivatedRoute, Params, Data } from '@angular/router';
import { CloudinaryOptions, CloudinaryUploader } from 'ng2-cloudinary';

/* Services */
import { ProjectsService } from '../../Services/Projects/projects.service';
import { TokenService } from '../../Services/Authentication/token.service';
import { PicturesService } from '../../Services/Pictures/pictures.service';
// import { UploadService } from '../../Services/Upload/upload.service';

/* Interface */
import { categories } from '../../Interfaces/Constants/categories-constant';

/* Animations */
import { dialog, background } from '../../Animations/animations';

@Component({
	selector: 'app-create-projects',
	templateUrl: './create-projects.component.html',
	styleUrls: ['./create-projects.component.scss'],
	animations: [dialog, background],
	providers: [ProjectsService]
})
export class CreateProjectsComponent implements OnInit {

	admin	 		: boolean = false
	show1 			: boolean = true
	show2 			: boolean = false
	show3 			: boolean = false
	show4 			: boolean = false
	show5 			: boolean = false
	uploadingCP 	: boolean = false
	uploadingLP 	: boolean = false
	project 		: any = {}
	location 		: any
	displayLocation : string
	cover_picrure 	: string
	pictureType 	: string
	buttonText 		: string = 'Save'
	visibility 		: boolean = true
	categories 		: Object[] = categories
	start 			: number = 0
	long 			: number
	lat 			: number

	cloudinaryImage: any;

	uploader: CloudinaryUploader = new CloudinaryUploader(
		new CloudinaryOptions({ cloudName: 'dqpkpmrgk', uploadPreset: 'z7rzegb5', autoUpload: true })
	);

	constructor(private ProjectsService: ProjectsService,
				private TokenService: TokenService,
				private route: ActivatedRoute,
				private router: Router,
				private PicturesService: PicturesService) {
		this.route.queryParams.subscribe(params => {
			if (params['id']) {
				if (params['start'] == 1)
					this.buttonText = 'Next'
				this.initProject(params['id']);
			} else
				this.router.navigate(['/']);
		});

		this.imageUpload();
	}

	ngOnInit() {
	}

	/* INIT PROJECT */
	initProject(public_id) {
		this.ProjectsService.getProject(public_id).subscribe( res => {
			if (!res.project) return this.router.navigate(['/']);

			this.project 				= res.project

			this.visibility 			= this.project.project_visibility
			if (this.project.state)
				this.displayLocation 	= this.project.city + ', ' + this.project.state + ', ' + this.project.country
			else
				this.displayLocation 	= this.project.city + ', ' + this.project.country

			if (this.TokenService.getToken()) {
				const token = this.TokenService.getToken()
				this.admin 	= token.user.moderator ? true : false
				if (token.user.id !== this.project['profile_id'] && !this.admin)
					this.router.navigate(['/']);
			} else
				this.router.navigate(['/']);
		});
	}

	/* UPLOAD */
	imageUpload() {
		this.uploader.onSuccessItem = (item: any, response: string, status: number, headers: any) => {
			this.cloudinaryImage = JSON.parse(response);
			if (this.pictureType === 'cover')
				this.project.picture = this.cloudinaryImage.secure_url
			else {
				this.PicturesService.uploadPicture('project', 'logo', { url: this.cloudinaryImage.secure_url })
					.subscribe(res => {
						if (res.picture)
							this.project.logo 	= res.picture
					});
			}
			this.uploadingCP = this.uploadingLP = false
		};

		this.uploader.onAfterAddingFile = (fileItem) => {
			if (this.pictureType === 'cover')
				this.uploadingCP = true
			else
				this.uploadingLP = true
			this.uploader.uploadAll();
		};

		this.uploader.onProgressItem = (fileItem, progress) => {
			console.log(progress);
		}
	}

	setPictureType(type) {
		this.pictureType = type;
	}

	storyChange(event) {
		this.project['1st_description'] = event.resume_1
		this.project['2nd_description'] = event.resume_2
		this.project.video 				= event.video
		this.project.pictures 			= event.pictures
	}

	getAddress(place: Object) {
		this.long 		= place['geometry'].location.lat();
		this.lat 		= place['geometry'].location.lng();
		this.location 	= place['formatted_address'].replace('USA', 'United States')
	}

	selectCategory(category) {
		this.project.category = category.name
	}

	getCategoryIndex(category) {
		if (category) {
			const length = categories.length
			for (let i = 0; i < length; i++) {
				if (categories[i].name === category)
					return categories[i].id
			}
			return 0
		} else
			return 0
	}

	/* UPDATE PROJECT */
	updateProject() {
		this.ProjectsService.updateProject(this.project['id'], 	this.checkBody()).subscribe( res => {
			if (res.success) {
				if (this.buttonText === 'Save') {
					this.buttonText = 'Saved'
					setTimeout( () => {
						this.buttonText = 'Save'
					}, 2000);
				} else
					this.showNextSection(this.start);
			}
		});
	}

	removeProject() {
		this.ProjectsService.deleteProject(this.project['id'], {}).subscribe( res => {
			if (res.success)
				return this.router.navigate(['/']);
		});
	}

	checkBody() {
		const locationArray: Array<any> = this.checkLocation(this.displayLocation) || [];
		const body = {
			title 				: this.project.title || '',
			category_id			: this.getCategoryIndex(this.project.category),
			desc_1 				: this.replaceNewline(this.project['1st_description']) || 'd',
			desc_2 				: this.replaceNewline(this.project['2nd_description']) || 'd',
			picture 			: this.project.pictures[0],
			pictures 			: this.project.pictures || [],
			description 		: this.project.description || '',
			about 				: this.project.about,
			video 				: this.project.video,
			project_visibility	: this.visibility = this.visibility ? true : false,
			link 				: this.project.link || '',
			app 				: this.project.app || '',
			logo 				: this.project.logo,
			location 			: {
				city 			: locationArray[0],
				state 			: locationArray[1],
				country 		: locationArray[locationArray.length - 1],
				lng 			: this.long,
				lat 			: this.lat
			}
		}
		this.location = ''
		return body
	}

	checkLocation(location) {
		if (this.location)
			return this.location.split(', ');
		else
			return location.split(', ');
	}

	replaceNewline(text) {
		return text.replace(/\r?\n/g, '<br />');
	}

	/* ROUTING */
	backToProject() {
		this.router.navigate(['/project', this.project['public_id'], this.project['title'].replace(/ /g, '-')]);
	}

	hideAllShow() {
		this.show1 = this.show2 = this.show3 = this.show4 = this.show5 = false
	}

	/* SHOW SECTION */
	showCreateProjectSection(index) {
		this.hideAllShow();

		if (index === 1) {
			this.show1 = true
		} else if (index === 2) {
			this.show2 = true
		} else if (index === 3) {
			this.show3 = true
		} else if (index === 4) {
			this.show4 = true
			this.buttonText = 'Publish'
		}
	}

	showNextSection(index) {
		if (this.show1) {
			this.show1 = false
			this.show2 = true
		} else if (this.show2) {
			this.show2 = false
			this.show3 = true
		} else if (this.show3) {
			this.show3 = false
			this.show4 = true
			this.buttonText = 'Publish'
		} else {
			this.router.navigate([
				'/project', this.project.public_id, this.project.title.replace(/ /g, '-')
				]);
		}
	}
}



